header {
  position: relative;
  height: 300px;
  background-image: linear-gradient(#ff9d2f, #ff6126);
  border-bottom-left-radius: 50% 20%;
  border-bottom-right-radius: 50% 20%;
}

h1 {
  margin: 0;
  padding: 25px 0;
  font: 18px "Arial";
  text-align: center;
  font-style: bold;
}

header h1 {
  color: white;
}
